.dashboard-admin {
    width: 100%; height: 100%;
}

.dashboard-header {
    height: 10%;
    display: flex;
    align-items: center; 
    padding: 0 1rem;
    background-color: rgb(127, 212, 194);
}
.dashboard-header h1 {
    font-size: 16px;
}

.dashboard-container {
    height: 90%;
    display: flex;
}
.dashboard-container .dashboard-sidebar {
    width: 20%; 
    padding: 1rem;
    display: flex; flex-direction: column;
    align-items: center;
    background-color: rgb(54, 54, 54);
}
.dashboard-container .dashboard-sidebar li {
    list-style: none;
}
.dashboard-container .dashboard-sidebar li a {
    font-size: 18px;
    font-weight: 400;
    text-decoration: none;
    color: white;
}

.dashboard-container .dashboard-panel {
    width: 80%;
    background-color:rgb(184, 184, 184) ;
}
